<template>
  <div class="roles">
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary">添加角色</el-button>
        </el-col>
      </el-row>
      <!-- 角色列表区域 -->
      <el-table :data="rolesList" border stripe>
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(item,index) in scope.row.children"
              :key="item.id"
              :class="['bdbottom',index==0?'bdtop':'','vcenter']"
            >
              <!-- 渲染一级权限 -->
              <el-col :span="5">
                <el-tag closable @close="removeRoles(scope.row,item.id)">{{ item.authName }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染二级权限 -->
              <el-col :span="19">
                <!-- 通过for循环渲染二级权限 -->
                <el-row
                  v-for="(item2,i2) in item.children"
                  :class="[i2 == 0 ? '':'bdtop','vcenter']"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRoles(scope.row,item2.id)"
                    >{{ item2.authName }}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 通过for循环渲染三级权限 -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRoles(scope.row,item3.id)"
                    >{{ item3.authName }}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <!-- <pre>{{ scope.row }}</pre> -->
          </template>
        </el-table-column>
        <!-- 索引列 -->
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" icon="el-icon-edit">编辑</el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete">删除</el-button>
            <el-button
              type="warning"
              size="mini"
              icon="el-icon-setting"
              @click="showSetRolesDialog(scope.row)"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 分配权限对话框 -->
    <el-dialog title="分配权限" :visible.sync="setRolesDialogVisible" width="50%" @close="setRolesDialogClosed">
      <!-- 树形控件 -->
      <el-tree
        :data="rightList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRolesDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allutRights">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rolesList: [],
      // 分配权限对话框的显示与隐藏
      setRolesDialogVisible: false,
      rightList: [],
      //树形控件的属性对象
      treeProps: {
        children: "children",
        label: "authName"
      },
      //默认勾选的节点的 key 的数组
      defKeys: [],
      roleId:''
    };
  },
  created() {
    // 调用角色列表数据
    this.getRolesList();
  },
  methods: {
    // 获取角色列表数据
    async getRolesList() {
      let { data: res } = await this.$http.get("roles");
      if (res.meta.status !== 200) {
        this.$message.error("获取角色列表数据失败");
      } else {
        this.rolesList = res.data;
        //   console.log(res)
      }
    },
    // 根据id进行删除
    async removeRoles(role, rightsId) {
      const confirmRoles = await this.$confirm(
        "此操作将永久删除该文件, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);
      if (confirmRoles !== "confirm") {
        this.$message.info("取消了删除");
      } else {
        let { data: res } = await this.$http.delete(
          `roles/${role.id}/rights/${rightsId}`
        );
        if (res.meta.status !== 200) {
          this.$message.error("删除权限失败!");
        } else {
          role.children = res.data;
        }
      }
    },
    // 展示分配权限的对话框
    async showSetRolesDialog(role) {
        this.roleId = role.id
      let { data: res } = await this.$http.get("rights/tree");
      if (res.meta.status !== 200) {
        this.$message.error("获取分配权限数据失败");
      } else {
        this.rightList = res.data;
      }
    // 递归获取三级节点id
    this.getLeafKeys(role,this.defKeys)
      this.setRolesDialogVisible = true;
    },
    // 通过递归的方式获取角色下所有三级权限id，并保存到defKeys数组中
    getLeafKeys(node,arr){
        // 如果当前的node不包含children属性，则是三级节点
        if(!node.children){
            arr.push(node.id)
        }else{
            node.children.forEach(item =>{
                this.getLeafKeys(item,arr)
            })
        }
    },
    // 分配权限关闭对话框清空数组的id
    setRolesDialogClosed(){
        this.defKeys = []
    },
    // 为角色分配权限
   async allutRights(){
        const keys = [
            ...this.$refs.treeRef.getCheckedKeys(),
            ...this.$refs.treeRef.getHalfCheckedNodes()
        ]
        const idStr = keys.join(',')
       let { data:res } = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
       if(res.meta.status !== 200){
           this.$message.error('分配权限失败')
       }else{
            // 调用角色列表数据
           this.getRolesList()
           this.setRolesDialogVisible = false
       }
    }
  }
};
</script>

<style lang="less">
.roles {
  .el-tag {
    margin: 7px;
  }
  .bdtop {
    border-top: 1px solid #eee;
  }
  .bdbottom {
    border-bottom: 1px solid #eee;
  }
}
</style>