<template>
  <div class="home">
    <el-container class="home-container">
      <!-- 头部区域 -->
      <el-header>
        <div>
          <img src="../assets/heima.png" alt />
          <span>电商后台管理系统</span>
        </div>
        <el-button type="info" @click="loginOut">退出</el-button>
      </el-header>
      <!-- 页面主体区域 -->
      <el-container>
        <!-- 左侧导航区域 -->
        <el-aside :width="isColl?'64px':'200px'">
          <!-- 侧边栏菜单 -->
          <div class="tag-button" @click="togg">|||</div>
          <el-menu
            background-color="#353c3c"
            text-color="#fff"
            active-text-color="#3A9FFF"
            unique-opened
            :collapse="isColl"
            :collapse-transition="false"
            router
            :default-active="actives"
          >
            <el-submenu :index="item.id+''" v-for="item of menuList" :key="item.id">
              <!-- 导航一 -->
              <template slot="title">
                <!-- 图标 -->
                <i :class="iconObject[item.id]"></i>
                <!-- 文字内容 -->
                <span>{{ item.authName }}</span>
              </template>
              <!-- 导航二 -->
              <el-menu-item :index="'/'+v.path" v-for="v of item.children" :key="v.id" @click="sis('/'+v.path)">
                <!-- 图标 -->
                <i class="el-icon-menu"></i>
                <!-- 文字内容 -->
                <span>{{ v.authName }}</span>
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <!-- 右侧内容区域 -->
        <el-main>
          <!-- 路由占位符 -->
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 左侧菜单数据
      menuList: [],
      iconObject: {
        "125": "iconfont icon-users",
        "103": "iconfont icon-tijikongjian",
        "101": "iconfont icon-shangpin",
        "102": "iconfont icon-danju",
        "145": "iconfont icon-baobiao"
      },
      isColl: false,
      actives:''
    };
  },
  created() {
    // 调用左侧菜单数据
    this.getMenuList();
    //  初始化高亮
    this.actives = window.sessionStorage.getItem('activePath')
  },
  methods: {
    //退出按钮
    loginOut() {
      window.sessionStorage.clear();
      this.$router.push("/login");
    },
    // 左侧菜单数据
    async getMenuList() {
      let { data: res } = await this.$http.get("menus");
      if (res.meta.status !== 200) {
        this.$message.error(res.meta.msg);
      } else {
        this.menuList = res.data;
        // console.log(res);
      }
    },
    // 点击折叠按钮把菜单栏收起来
    togg() {
      this.isColl = !this.isColl;
    },
    // 点击让当前行高亮
    sis(actives){
      window.sessionStorage.setItem('activePath',actives)
      this.actives = actives
    }
  }
};
</script>

<style lang="less">
.home {
  height: 100%;
  .home-container {
    height: 100%;
  }
  .el-header {
    background: #353c3c;
    display: flex;
    justify-content: space-between;
    padding-left: 0;
    align-items: center;
    color: #fff;
    font-size: 20px;
    div {
      display: flex;
      align-items: center;
      span {
        margin-left: 15px;
      }
    }
  }
  .el-aside {
    background: #353c3c;
    .el-menu {
      border-right: 0;
    }
  }
  .el-main {
    background: #e8ecef;
  }
  .iconfont {
    margin-right: 10px;
  }
  .tag-button {
    background: #4a5064;
    font-size: 10px;
    line-height: 24px;
    text-align: center;
    color: #fff;
    letter-spacing: 0.2em;
    cursor: pointer;
  }
}
</style>